<template>
  <div class="modal-background" role="dialog" aria-modal="true" tabindex="-1">
    <form class="modal" @submit.prevent="onSubmit">
      <a href="" class="close" @click.prevent="closeModal">
        <CloseIcon/>
      </a>

      <div class="modal-title">  </div>

      <div class="modal-fields">
        <div class="modal-group">
          <div class="modal-group-subtitle"> </div>
          <div class="modal-group-radios">

            <div class="modal-group-radio">
              <input type="radio" v-model="type" id="type-1" value="">
              <label for="type-1">
                <i class="circle"></i>
                <div class="name"></div>
              </label>
            </div>

            <div class="modal-group-radio">
              <input type="radio" v-model="type" id="type-2" value="  ">
              <label for="type-2">
                <i class="circle"></i>
                <div class="name">  </div>
              </label>
            </div>

            <div class="modal-group-radio">
              <input type="radio" v-model="type" id="type-3" value="  ">
              <label for="type-3">
                <i class="circle"></i>
                <div class="name">  </div>
              </label>
            </div>

            <div class="modal-group-radio">
              <input type="radio" v-model="type" id="type-4" value="  ">
              <label for="type-4">
                <i class="circle"></i>
                <div class="name">  </div>
              </label>
            </div>

            <div class="modal-group-radio">
              <input type="radio" v-model="type" id="type-5" value="">
              <label for="type-5">
                <i class="circle"></i>
                <div class="name"></div>
              </label>
            </div>

            <div class="modal-group-radio">
              <input type="radio" v-model="type" id="type-6" value="">
              <label for="type-6">
                <i class="circle"></i>
                <div class="name"></div>
              </label>
            </div>

            <div class="modal-group-radio">
              <input type="radio" v-model="type" id="type-7" value=",   ..">
              <label for="type-7">
                <i class="circle"></i>
                <div class="name">,   ..</div>
              </label>
            </div>

            <div class="modal-group-radio">
              <input type="radio" v-model="type" id="type-8" value="-">
              <label for="type-8">
                <i class="circle"></i>
                <div class="name">-</div>
              </label>
            </div>
          </div>

        </div>

        <div class="modal-group">
          <div class="modal-group-subtitle"> </div>
          <div class="modal-group-radios">

            <div class="modal-group-radio">
              <input type="radio" v-model="quantity" id="quantity-1" value="1-3 ">
              <label for="quantity-1">
                <i class="circle"></i>
                <div class="name">1-3 </div>
              </label>
            </div>

            <div class="modal-group-radio">
              <input type="radio" v-model="quantity" id="quantity-2" value=" 3-  /  ">
              <label for="quantity-2">
                <i class="circle"></i>
                <div class="name"> 3-  /  </div>
              </label>
            </div>

          </div>
        </div>

        <div class="modal-group">

          <div class="modal-group-inputs">
            <label class="modal-group-input">
              <div class="label">   </div>
              <input type="text" placeholder=" ..,  " v-model="bookAuthors">
              <div class="error">    </div>
            </label>
          </div>

        </div>
      </div>

      <div class="modal-footer">
        <a href="" class="modal-footer-back light-btn" @click.prevent="closeModal"></a>
        <button class="modal-footer-submit dark-btn"> </button>
      </div>
    </form>
    <a href="" class="hidden-closure-area" @click.prevent="closeModal"></a>
  </div>
</template>

<script>
import { taxi } from "../main";

import CloseIcon from '@/assets/ui/close.svg';

export default {
  components: {
    CloseIcon,
  },

  data() {
    return {
      type: '',
      quantity: '',
      bookAuthors: '',
      currentBook: {},
      currentIndexBook: '',
    }
  },

  methods: {
    onSubmit() {
      this.currentBook.title = this.bookAuthors;

      taxi.$emit('hiddenModal');
      taxi.$emit('replaceBook');
    },

    closeModal() {
      taxi.$emit('hiddenModal');
    }
  },

  mounted() {
    window.onkeydown = function( event ) {
      event.keyCode == 27 ? taxi.$emit('hiddenModal') : false;
    };
  },

  created() {
    taxi.$on('editBook', (book, index) => {
      this.currentIndexBook = index;
      this.currentBook = book;
      this.bookAuthors = book.title;
    });
  }
}
</script>

<style lang="scss" scoped>
@import '@/styles/mixin.scss';
@import '@/styles/variable.scss';
@import '@/styles/button.scss';

@import '@/styles/modal.scss';
</style>